


var modelTestView = {
	widgets : [],

    init: function() {
    	this.loadPage();
    },
    event: function() {
    	var _this = this;
        $$('#model_test_button').on('click', function() {
        	$.each(_this.widgets,function(index,item){
        		console.log(item.table("widget"));
        		item.table("refresh");
        	})
        });
    },
	loadPage:function(callback){
		var config = {
			fields : [{
				label : '设备编号',
				key : 'emCode'
			},{
				label : '设备型号',
				key : 'emModel'
			}],
			widgets : [{
				name : 'client.table',
				renderId : 'model_test_component_1',
				options : {
					dataAjaxUrl : '/restful/test/randomData',
					type : 'GET',
					paramters : {
						prifex : 'test2'
					},
					columns : [{
						label : '设备名称',
						dataIndex : 'test2_name',
					},{
						label : '设备状态',
						dataIndex : 'test2_status',
					},{
						label : '设备数量',
						dataIndex : 'test2_num',
					},{
						label : '设备数据',
						dataIndex : 'test2_data',
					}]
				}
				
			},
			{
				name : 'client.table',
				renderId : 'model_test_component_2',
				options : {
					dataAjaxUrl : '/restful/test/randomData',
					type : 'GET',
					paramters : {
						prifex : 'test1'
					},
					columns : [{
						label : '设备名称',
						dataIndex : 'test1_name',
					},{
						label : '设备状态',
						dataIndex : 'test1_status',
					},{
						label : '设备数量',
						dataIndex : 'test1_num',
					},{
						label : '设备数据',
						dataIndex : 'test1_data',
					}]
				}
			}]
		};
		this.render(config);
		this.event();
	},
	render : function(config){
		var fields = config["fields"] || [];
		var childComponents = config["components"] || [];
		$.each(fields,function(index,item){
			$("#model_test_fields").append("<span>"+item.label+"</span></span>"+item.key+"</span>");
		});
		var childWidgets = config["widgets"] || [];
		var _this = this;
		$.each(childWidgets,function(index,item){
			var widget = widgetManager.createWidget(item.name,item.options,$("#"+item.renderId));
			//console.log(widget);
			//widget.setOptions(item.options|| {});
			_this.widgets.push(widget);
		})
		
	}
}

F7.onPageInit('model_test', function (page) {
	console.log($.widget);
	modelTestView.init();
});



